.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}
body {
  background: #d8c8bb;
}
.wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-width: 1500px;
  margin: 0 auto;
  padding: 0;
  box-shadow: 0 -200px 35px #919191;
}
.brand-logo {
  background: url("../images/logo-primary.png");
  width: 75px;
  height: 45px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}
.section {
  padding: 8em 0;
}
.section.section-default {
  color: #000;
  background: white;
}
.section.section-primary {
  color: white;
  background: #e14012;
}
.section.section-primary a {
  text-decoration: underline;
  color: inherit;
}
.section.section-primary a.btn,
.section.section-primary .wcopc .woocommerce-checkout-payment a.button,
.wcopc .woocommerce-checkout-payment .section.section-primary a.button {
  text-decoration: none;
}
.section.section-info {
  color: white;
  background: #a5d2cd;
}
.section.section-info .well {
  color: #000;
}
.section.section-info .brand {
  color: #000;
}
.section.section-gray {
  padding: 4em 0 4em 0;
  color: white;
  background: #000;
}
.section.section-tabs {
  padding: 0 0 4em 0;
}
.section.section-tabs .tab-container {
  margin-top: -42px;
}
.section.section-tabs .tab-content {
  padding-top: 4em;
}
.section.section-related {
  padding: 4em 0 4em 0;
  color: #000;
}
.section.section-related a {
  text-decoration: underline;
  color: inherit;
}
.section.section-related hr {
  margin: 1em 0 2em 0;
  border-color: rgba(0, 0, 0, 0.15);
}
.section.section-base {
  background: #e0d3c3;
}
.section.section-light {
  background: #fafafa;
}
.section.section-archive {
  padding: 4em 0 8em 0;
}
.section.section-archive .row-video .post-categories {
  margin-top: 10px;
  margin-bottom: 10px;
  color: #777777;
}
